import React from 'react';
import PropTypes from 'prop-types';
import ApiKeyListContainer from '../../ApiKeysSidePaneComponents/ApiKeyList/ApiKeyListContainer';
import RetailApiKeysFormContainer from './RetailApiKeysFormContainer';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './RetailApiKeysComponent.css';

const retailApiKeysClasses = getBEMClasses(['retail-api-keys']);

const RetailApiKeysComponent = () => {
  return (
    <div className={retailApiKeysClasses()}>
      <div className={retailApiKeysClasses('content')}>
        <RetailApiKeysFormContainer />
        <ApiKeyListContainer customClass={'retail-api-key-list'} />
      </div>
    </div>
  );
};

RetailApiKeysComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailApiKeysComponent;



