import { connect } from 'react-redux';
import RemoveApiKeyModal from './RemoveApiKeyModal';
import { removeApiKey } from '../../../redux/actions/apiKeyActions';
import modalDecorator from '../../../hocs/modalDecorator';
import { MODAL_TYPES } from '../../../redux/actions/modalActions';

const mapDispatchToProps = {
  removeApiKey
};

const container = connect(
  null,
  mapDispatchToProps
)(RemoveApiKeyModal);

export default modalDecorator({
  name: MODAL_TYPES.REMOVE_API_KEY
})(container);



