import React from 'react';
import PropTypes from 'prop-types';
import Modal from '../../common/Modal/Modal';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './RemoveApiKeyModal.css';

const removeApiKeyModalClasses = getBEMClasses('remove-api-key-modal');

const RemoveApiKeyModal = (props, context) => {
  return (
    <Modal
      isOpen={props.isOpen}
      title={context.t('Delete API Key')}
      customClass="retail-api-keys-modal-delete"
      close={props.close}
      footer={{
        buttonText: context.t('Disable'),
        buttonStyle: 'subtractive',
        onClick: () => props.removeApiKey(props.apiKey)
      }}>
      <div className={removeApiKeyModalClasses('text')}>
        {context.t('Are you sure you want to delete this API Key?')}
      </div>
    </Modal>
  );
};

RemoveApiKeyModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  apiKey: PropTypes.string,
  removeApiKey: PropTypes.func.isRequired,
  close: PropTypes.func.isRequired
};

RemoveApiKeyModal.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RemoveApiKeyModal;



