import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../../common/APButton';
import config from '../../../config';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './RetailApiKeysFormComponent.css';

const retailApiKeysFormClasses = getBEMClasses('retail-api-keys-form');

const RetailApiKeysFormComponent = (props, context) => {
  const { openApiKeysSidePane } = props;
  return (
    <div className={retailApiKeysFormClasses()}>
      <div className={retailApiKeysFormClasses('info')}>
        <div className={retailApiKeysFormClasses('header-text')}>
          {context.t('About API Keys')}
        </div>
        <div className={retailApiKeysFormClasses('text')}>
          {context.t(
            'The Public API is available in HTTP, WebSocket, and Get Request, with streaming data on products, product pairs, ticker activity, trades, and the order book.'
          )}
        </div>
        <div className={retailApiKeysFormClasses('text')}>
          {context.t(
            "The Private API is accessible only by registered users and enables access and control of a user's account. All key user functions may be executed from the API, including account and order management activities."
          )}
        </div>
      </div>

      <div className={retailApiKeysFormClasses('actions')}>
        <div className={retailApiKeysFormClasses('create-new')}>
          <div className={retailApiKeysFormClasses('create-new-header')}>
            {context.t('Create New:')}
          </div>
          <APButton
            type="submit"
            customClass={retailApiKeysFormClasses()}
            onClick={openApiKeysSidePane}>
            {context.t('API Key')}
          </APButton>
        </div>

        <div className={retailApiKeysFormClasses('documentation')}>
          <a href={config.ApiKeys.documentationLink} target="blank">
            {context.t('View: API Documentation »')}
          </a>
        </div>
      </div>
    </div>
  );
};

RetailApiKeysFormComponent.propTypes = {
  openApiKeysSidePane: PropTypes.func.isRequired
};

RetailApiKeysFormComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailApiKeysFormComponent;



